<template>
    <div>
        <div class="box">
            <Head>
                <router-link to="/List" slot="left">
                    <img src="../../images/110.png" alt="">
                </router-link>
                <span slot="center">账号注册</span>
            </Head>
            <div class="con">
                <div class="span">
                    <input class="user" type="text" placeholder="账号" v-model="username" />
                    <input class="pass" v-show="!value2" type="password" placeholder="密码" v-model="password" />
                    <input class="pass" v-show="value2" type="text" placeholder="密码" v-model="password" />
                    <input class="yan" type="text" placeholder="验证码">
                    <span>{{this.num}}</span>
                    <span>看不清</span>
                    <span @click="chang">换一张</span>
                    <el-switch
                        id="an"
                        class="an"
                        v-model="value2"
                        active-color="#13ce66"
                        inactive-color="#ff4949">
                    </el-switch>
                </div>
                <span class="wenxin">温馨提示：未注册过的账号，登陆时将自动注册</span>
                <span class="tishi">注册过的用户可凭账号账号密码登陆</span>
                <button @click="sub">注册</button>
                <p>重置密码？</p>
            </div>
        </div>
    </div>
</template>

<script>
import Head from '../../components/Header.vue'
export default {
    components:{
        Head
    },
    data(){
        return{
            username:'',
            password:'',
            isWorks:false,
            num:'',
            value1: true,
            value2: false,
        }
    },
    methods:{
        sub(){
              let a = '';
            for(var i = 0 ; i < 6 ; i++ ){
                a+=parseInt(Math.random()*10)
            }
            localStorage.setItem("a",a)
            this.$router.push("List")
            // if(localStorage.getItem(this.username)===null){
            //     localStorage.setItem(this.username,this.password)
            //     this.isWorks = true
            //     setTimeout(()=>{
            //         this.$router.push({name:'Login'})
            //     },2000)
            // }
           
        },
        chang(){
            let a = '';
            for(var i = 0 ; i < 4 ; i++ ){
                a+=parseInt(Math.random()*10)
            }
            this.num = a;
        }
    },
    mounted(){
        let a = '';
        for(var i = 0 ; i < 4 ; i++ ){
            a+=parseInt(Math.random()*10)
        }
        this.num = a;
    }
}
</script>

<style lang="less" scoped>
    .box{
        width: 100%;
        // height: 7.9rem;
        background: #F5F5F5;
        header{
            width: 100%;
            height: 1rem;
            background: #3792E5;
            position: relative;
            img{
                width: 0.4rem;
                position: absolute;
                top: 0.3rem;
                left: 0.2rem;
            }
            span{
                display: block;
                position: absolute;
                top: 0.25rem;
                color: white;
                font-size: 0.4rem;
                left: 2.8rem;
            }
        }
        .con{
            width: 100%;
            // height: 0.9rem;
            .span{
                width: 100%;
                height: 3.5rem;
                display: block;
                position: relative;
                input{
                    width: 100%;
                    height: 1.1rem;
                    text-indent: 0.2rem;
                    border-left: none;
                    border-right: none;
                    border-top: none;
                    font-size: 0.35rem;
                    color: #A9A9A9;
                }
                .user{
                   position: absolute;
                   top: 0.2rem;
                }
                .pass{
                    position: absolute;
                    top: 1.35rem;
                }
                .yan{
                    position: absolute;
                    top: 2.5rem;
                    margin-left: 0;
                }
                span{
                    display: block;
                    font-size: 0.35rem;
                    position: absolute;
                    top: 2.8rem;
                    right: 1.7rem;
                    // padding: 5rem;
                }
                span:nth-of-type(2){
                    display: block;
                    font-size: 0.3rem;
                    position: absolute;
                    top: 2.6rem;
                    right: 0.5rem;
                }
                span:nth-of-type(3){
                    display: block;
                    font-size: 0.3rem;
                    position: absolute;
                    top: 3.1rem;
                    right: 0.5rem;
                    color: #3792E5;
                }
                .an{
                    position: absolute;
                    top: 1.75rem;
                    right: 0.5rem;
                }
            }
            .wenxin{
                display: block;
                color: #FF0000;
                font-size: 0.25rem;
                margin: 0.4rem 0 0 0.2rem;
            }
            .tishi{
                display: block;
                color: #FF0000;
                font-size: 0.25rem;
                margin: 0.2rem 0 0 0.2rem;
            }
            button{
                width: 6.4rem;
                height: 0.7rem;
                border: none;
                margin-left: 0.5rem;
                background: #4CD964;
                color: white;
                font-size: 0.35rem;
            }
            p{
                color: #3B95E9;
                font-size: 0.3rem;
                float: right;
                padding-right: 0.4rem;
            }
        }
    }
</style>


